#fp-nav {
    margin-top: 10px !important;
    top: 0;
    ul {
        li {
            a {
                span:last-child {
                    background: #DCDCDC;
                }
                &.active {
                    span:last-child {
                        background: #fff;
                    }
                }
            }
        }
    }
}